<template>
  <div id="app">
    <header class="header">
      <label class="g-button uploader__label j-file-label">
        <input
          class="uploader__input j-file-input"
          type="file" @change="getFile" multiple
        >
        <span class="j-file-name">Обрати файли</span>
      </label>
      <div class="search">
        Пошук по: <input type="text" v-model="filter">
      </div>
    </header>
    <div
      v-for="(file, index) in sortedItems"
      :key="index"
      class="table__wrapper"
    >
      <div class="Name">{{file[1].B }}</div>
      <table>
        <tr
          v-for="(row, rowNum) in file"
          :key="index / 1000 + rowNum"
          :class="{'Title': rowNum === '3'}"
        >
          <template v-if="rowNum !== '1' && rowNum !== '2'">
            <td
              v-for="(cell, key) in row"
              :key="key"
            >
              {{ cell }}
            </td>
          </template>
        </tr>
        <tr>
          <td
            v-for="(cell, key) in file[3]"
            :key="key"
            :class="{'sum': getSum(index, key)}"
          >
          {{ getSum(index, key) }}
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import XLSX from "xlsx";

export default {
  name: "app",

  data: () => ({
    items: [],
    filter: ''
  }),

  computed: {
    sortedItems: function() {
      const arr = [];
      for (let file of this.items) {
        let obj = {};
        for (let row in file) {
          if (+row === 1 || +row === 2 || +row === 3) obj[row] = file[row];
          if (file[row].B && file[row].B.toLowerCase().indexOf(this.filter.toLowerCase()) !== -1) {
            obj[row] = file[row];
          }
        };
        if (Object.keys(obj).length > 3) {
          arr.push(obj);
        }
      };
      return arr;
    },
  },

  methods: {
    getFile(e) {
      this.items = [];
      e.stopPropagation();
      e.preventDefault();
      const files = e.target.files;
      const reg = new RegExp('^[0-9]+$');
      const CONTEXT = this;

      for (let file of files) {
        let reader = new FileReader();
        reader.onload = function(e) {
          var data = e.target.result;

          /* if binary string, read with type 'binary' */
          var workbook = XLSX.read(data, { type: "binary" });
          // console.log(file.name);
          // console.log(workbook);
          const cells = workbook.Sheets[workbook.SheetNames[0]];
          let rows = {};
          // console.log(cells);
          rows['1'] = {
            B: file.name
          };

          const header = {};
          for (let cell in cells) {
            if (reg.test(cell.substring(1)) && cell.substring(1) === '3') {
              header[cell.substring(0, 1)] = true;
            }
          };

          for (let cell in cells) {
            if (reg.test(cell.substring(1))) {
              rows[cell.substring(1)] = rows[cell.substring(1)] || {};
              for (let th in  header) {
                rows[cell.substring(1)][th] = '';
              }
            }
          }

          for (let cell in cells) {
            if (reg.test(cell.substring(1))) {
              if (typeof cells[cell].v === 'string') {
                rows[cell.substring(1)][cell.substring(0,1)] = cells[cell].v.replace(/\s+/g,' ').trim();;
              } else {
                rows[cell.substring(1)][cell.substring(0,1)] = cells[cell].v;
              }
            }
          }



          CONTEXT.items.push(rows);
        };
        reader.readAsBinaryString(file);
      }
    },

    getSum(tableIndex, collkey) {
      const reg = new RegExp('^[0-9]*[.,]?[0-9]+$');
      let rows = this.sortedItems[tableIndex];
      let sum = 0;
      if (collkey === 'A' || collkey === 'B') return '';
      for (let row in rows) {
        if (reg.test( rows[row][collkey])) {
          sum += +rows[row][collkey];
        }
      }

      return (Math.ceil((sum)*1000)/1000) || '';
    }
  }
};
</script>

